// 页面路由处理
export const pageRouter = {
  mounted: function() {
  },
  data() {
    return {
    }
  },
  methods: {
    // 跳转到某一页面
    goPage(name, params) {
      this.$store.dispatch('router/setPageParams', params)
      this.$router.push({
        name: name,
        params: { 'keyName': params }
      })
    },
    // 获取页面参数
    getParams() {
      // this.getParams = this.$route.params
      // this.itemDetail = this.getParams.itemDetail
      return this.$store.state.router.pageParams
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 因为当钩子执行前，组件实例还没被创建
      // vm 就是当前组件的实例相当于上面的 this，所以在 next 方法里你就可以把 vm 当 this 来用了。
      // vm.$store.commit('router/add', to.name)
      // console.log('beforeRouteEnter=index==', to.name)// 当前组件的实例
      if (to.name !== 'MhHome' && to.name !== 'MallHome') {
        var list = vm.$store.state.router.keepAlive
        const index = list.indexOf(to.name)
        // console.log('beforeRouteEnter=name==', to.name)// 当前组件的实例
        if (index === -1) {
          // list.splice(index, 1)
          list.push(to.name)
        }
        vm.$store.commit('router/SET_KEEP_ALIVE', list)
      } else {
        vm.$store.commit('router/SET_KEEP_ALIVE', [])
      }
      console.log('PageRoute=进入页面==', list)// 当前组件的实例
    })
  },
  // beforeRouteUpdate(to, from, next) {
  //   next()
  // },
  beforeRouteLeave(to, from, next) {
    var list = []
    list = this.$store.state.router.keepAlive
    if (from.name === 'GysXmbm') {
      const formIndex = list.indexOf(from.name)
      list.splice(formIndex, 1)
    }
    const formIndex = list.indexOf(from.name)
    const toIndex = list.indexOf(to.name)
    if (toIndex !== -1) {
      list.splice(formIndex, 1)
    }
    const rootpage = []
    for (let i = 0; i < this.$store.state.router.footMeun.length; i++) {
      rootpage.push(this.$store.state.router.footMeun[i].component)
    }
    const formRootIndex = rootpage.indexOf(from.name)
    const toRootIndex = rootpage.indexOf(to.name)
    if (formRootIndex !== -1 && toRootIndex !== -1) {
      list.splice(formIndex, 1)
    }
    console.log('PageRoute=退出页面======================', list)// 当前组件的实例
    this.$store.commit('router/SET_KEEP_ALIVE', list)
    next()
  }
}
