<template>
  <div>
    <!-- header begin -->
    <div class="nav-bar">
      <div class="nav-bar-left" @click="goShglIndex()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">发货信息</div>
      <div class="nav-bar-right">
        <div class="right-option blue" @click="showDeleteBtn()">
          提交 <i class="iconfont iconxieruqianming" />
        </div>
      </div>
    </div>
    <!-- header end -->
    <div v-if="xm_fhdh.length>0" class="container-tab">
      <van-sticky class="tabs-sticky">
        <van-tabs v-model="active" swipeable animated @click="onClick" @disabled="onClickDisabled">
          <van-tab title="编辑发货到货">
            <div class="container-mini">
              <div class="content">
                <div class="common-main box">
                  <div v-for="(item,index) of xm_fhdh" :key="index">
                    <div v-if="item.type!=='hidden'">
                      <van-field
                        v-if="item.type==='text'"
                        v-model="item.value"
                        v-validate="item.required?'required':''"
                        :required="item.required"
                        :name="item.name"
                        :label="item.showName"
                        input-align="right"
                        :error-message="scState?errors.first(item.name):''"
                        label-width="100"
                        :placeholder="'请输入'+item.showName"
                      />
                      <van-field
                        v-if="item.type==='calendar'"
                        v-model="item.value"
                        v-validate="item.required?'required':''"
                        label-width="100"
                        input-align="right"
                        readonly
                        :name="item.name"
                        :label="item.showName"
                        :error-message="scState?errors.first(item.name):''"
                        :placeholder="'请选择'+item.showName"
                        :required="item.required"
                        is-link
                        @click="showFhsjCalendar(index)"
                      />
                      <van-field
                        v-if="item.type==='select'"
                        v-model="item.value"
                        readonly
                        input-align="right"
                        label-width="100"
                        :name="item.name"
                        :label="item.showName"
                        :error-message="errors.first(item.name)"
                        :placeholder="'请选择'+item.showName"
                        is-link
                        @click="showWllxPopup(index)"
                      />
                      <van-field
                        v-if="item.type==='label'"
                        v-model="item.value"
                        readonly
                        input-align="right"
                        label-width="100"
                        :label="item.showName"
                        placeholder="草稿"
                      />
                      <div v-if="item.type==='file'" class="page-list">
                        <div class="pagelist-left">附件</div>
                        <div class="pagelist-right" style="margin-left: 0">
                          <XyUploadPlus
                            :key="key"
                            ref="uploadEl"
                            style="width:100%;"
                            filed="fj"
                            :filegroup="filegroup"
                            @getFileChangeInfo="getFileChangeInfo"
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                  <!-- <van-field
                    v-if="fhdbh"
                    v-model="fhdbh"
                    label="发货单编号"
                    input-align="right"
                    label-width="100"
                    readonly
                    placeholder="不可编辑，由系统自动生成"
                  />
                  <van-field
                    v-if="fhdbh"
                    v-model="fhdbh"
                    label="发货单编号"
                    input-align="right"
                    label-width="100"
                    readonly
                    placeholder="不可编辑，由系统自动生成"
                  />
                  <van-field
                    v-model="wllxShow"
                    readonly
                    input-align="right"
                    label-width="100"
                    name="wllx"
                    label="物流类型"
                    :error-message="errors.first('wllx')"
                    placeholder="请选择物流类型"
                    is-link
                    @click="showWllxPopup()"
                  />
                  <van-field
                    v-model="fhdmc"
                    v-validate="'required'"
                    name="fhdmc"
                    input-align="right"
                    label-width="100"
                    required
                    label="发货单名称"
                    :error-message="errors.first('fhdmc')"
                    placeholder="请输入发货单名称"
                  />
                  <van-field v-model="wlgsmc" label="物流公司名称" input-align="right" label-width="100" placeholder="请输入物流公司名称" />
                  <van-field
                    v-model="date"
                    v-validate="'required'"
                    label-width="100"
                    input-align="right"
                    readonly
                    name="fhsj"
                    label="发货时间"
                    :error-message="errors.first('fhsj')"
                    placeholder="请选择发货时间"
                    required
                    is-link
                    @click="showFhsjCalendar()"
                  />
                  <van-field
                    v-model="ydh"
                    v-validate="'required'"
                    label="物流运单号"
                    required
                    name="ydh"
                    input-align="right"
                    label-width="100"
                    :error-message="errors.first('ydh')"
                    placeholder="请输入物流运单号"
                  />
                  <van-field
                    v-model="fhdd"
                    v-validate="'required'"
                    name="fhdd"
                    required
                    label="发货地点"
                    :error-message="errors.first('fhdd')"
                    input-align="right"
                    label-width="100"
                    placeholder="请输入发货地点"
                  />
                  <van-field
                    v-model="wllxr"
                    label="物流联系人"
                    input-align="right"
                    label-width="100"
                    placeholder="请输入物流运联系人"
                  />
                  <van-field
                    v-model="date1"
                    v-validate="'required'"
                    label-width="100"
                    input-align="right"
                    readonly
                    name="yjdhsj"
                    label="预计到货时间"
                    placeholder="请选择预计到货时间"
                    :error-message="errors.first('yjdhsj')"
                    required
                    is-link
                    @click="showFhCalendar()"
                  />
                  <van-field
                    v-model="wllxfs"
                    v-validate="'phone'"
                    label="联系方式"
                    type="number"
                    input-align="right"
                    label-width="100"
                    placeholder="请输入联系方式"
                    name="lxfs"
                    :error-message="errors.first('lxfs')"
                  />
                  <div class="page-list">
                    <div class="pagelist-left">附件</div>
                    <div class="pagelist-right">
                      <XyUploadPlus
                        ref="uploadEl"
                        style="width:100%;"
                        :filed="'fj'"
                        :filegroup="filegroup"
                        @getFileChangeInfo="getFileChangeInfo"
                      />
                    </div>
                  </div> -->
                </div>
              </div>
            </div>
          </van-tab>
          <van-tab title="商品信息" :disabled="!canChange">
            <div v-if="fhmxList.length>0" class="container-mini">
              <div v-for="(items,indexs) of fhmxList" :key="indexs" class="content">
                <div class="common-main box">
                  <!-- 循环列表 begin -->
                  <div class="general-list">
                    <div class="general-item" @click="showSpxzPopup(indexs)">
                      <div class="general-tit">{{ items.htmc }}</div>
                      <div class="general-info"><span><em>订单编号：</em>{{ items.htbh }}</span></div>
                    </div>
                    <div class="general-arrow"><i class="iconfont iconarrowright" /></div>
                  </div>
                  <!-- 循环列表 end -->
                </div>
              </div>
            </div>
            <div v-else class="container-mini">
              <div class="content">
                <div class="common-main box">
                  <div class="cart-empty">
                    <div class="cart-empty-btn"><van-button size="small" plain type="info" @click="goAddSp()">去添加商品</van-button></div>
                  </div>
                </div>
              </div>
            </div>
          </van-tab>
        </van-tabs>
      </van-sticky>
    </div>
    <div class="common-space" />
    <div v-if="active===0" class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="save()">保存</van-button></li>
      </ul>
    </div>
    <div v-if="active===1 && fhmxList.length>0" class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="goAddSp()">添加商品</van-button></li>
        <!-- <li><van-button type="info" round size="large" @click="save()">保存</van-button></li> -->
      </ul>
    </div>
    <!-- 物流类型弹出层 beign -->
    <searchPick
      :pick-title="searchPick.PickTitle"
      :pick-columns="searchPick.PickColumns"
      @confirmClick="confirmPick($event)"
    />
    <!-- 物流类型弹出层 end -->

    <!-- 发货日期选择 begin -->
    <van-calendar v-model="FhsjShow" color="#1890FF" @confirm="onConfirm" />
    <!-- 发货日期选择 begin -->

    <!-- 删除操作按钮 -->
    <div :class="showDelete == true ? 'foot-btn fixed show':'foot-btn fixed hide'" style="z-index:91;">
      <ul>
        <li class="check" @click="checkAll">
          <van-checkbox v-model="checked" />
          全选
        </li>
        <li><van-button type="info" round size="large">删除（25）</van-button></li>
      </ul>
    </div>

    <!-- 选择商品弹出层 beign -->
    <van-popup
      v-model="SpxzShow"
      :get-container="getContainer"
      closeable
      position="bottom"
      close-icon-position="top-left"
      :style="{ height: '90%',background:'#f9f9f9' }"
      @click="showSearch = false"
      @close="close"
    >
      <div class="popup-title">选择商品</div>
      <!-- <div class="popup-searchicon" @click.stop="showSearch=!showSearch"><i class="iconfont iconsousuo" /></div> -->
      <!-- 查询条件  begin -->
      <!-- <div :class="showSearch == true ? 'selectsp-search show':'selectsp-search hide'">
        <div class="common-main box">
          <div class="spe-input">
            <van-field v-model="xmmcvalue" label="项目名称" placeholder="请输入项目名称" input-align="right" />
            <van-field v-model="xmbhvalue" label="项目编号" placeholder="请输入项目编号" input-align="right" />
          </div>
          <div class="search-popup-btn">
            <van-button type="info" size="small" block round>搜索</van-button>
          </div>
        </div>
      </div> -->
      <!-- 查询条件  end -->

      <div v-if="fhmxList && fhmxList[mxIndex]" class="container-mini">
        <div class="content">
          <!-- 商品列表 begin -->
          <div v-for="(item,index) of fhmxList[mxIndex].mxList" :key="index" class="common-main box">
            <!-- 通用标题 beign -->
            <div class="title-check">
              <!-- <div class="check"><van-checkbox v-model="item.state" @click="checkSp(item,index)" /></div> -->
              <div class="check-info">{{ item.spmc }}</div>
            </div>
            <!-- 通用标题 end -->
            <div class="page-list">
              <div class="pagelist-left">订单数量</div>
              <div class="pagelist-right">{{ item.sl }}</div>
            </div>
            <div class="page-list">
              <div class="pagelist-left">待交货数量</div>
              <div class="pagelist-right">{{ item.djhsl }}</div>
            </div>
            <!-- <van-field v-model="item.bcsl" label="本次送货数量" input-align="right" label-width="100" placeholder="请输入数量" /> -->
            <div class="page-list">
              <div class="pagelist-left">本次送货数量</div>
              <div class="pagelist-right">{{ item.bcsl }}</div>
            </div>
            <div class="page-list">
              <div class="pagelist-left">单价（含税）元</div>
              <div class="pagelist-right">{{ item.dj }}</div>
            </div>
            <div class="page-list">
              <div class="pagelist-left">计量单位</div>
              <div class="pagelist-right">{{ item.jldw }}</div>
            </div>
            <div class="pagelist-btn">
              <ul>
                <li><van-button type="info" plain="" round @click="DeleteSp(item.id,index)">删除</van-button></li>
              </ul>
            </div>
          </div>
        </div>
        <!-- 商品列表 end -->
      </div>

      <!-- 底部按钮 begin -->
      <!-- <div class="foot-btn fixed">
        <ul>
          <li class="check" @click="checkAll">
            <van-checkbox v-model="checked" />
            全选
          </li>
          <li><van-button type="info" round size="large" @click="checkFinish()">完成（{{ checkedMxList.length }}）</van-button></li>
        </ul>
      </div> -->
      <!-- 底部按钮 end -->

    </van-popup>
    <!-- 选择商品弹出层 end -->

  </div>
</template>

<script land="script" scoped>
import searchPick from '@/components/searchComponents/searchPick.vue'
import XyUploadPlus from '@/components/upload/uploadPlus'
import { pageRouter } from '@/common/js/mixin.js'
export default {
  components: {
    searchPick,
    XyUploadPlus
  },
  mixins: [pageRouter],
  data() {
    return {
      key: 0,
      scState: false,
      xxIndex: '',
      xm_fhdh: [],
      id: '',
      fhmxList: [],
      checkedMxList: [], // 选中的商品明细信息
      mxIndex: 0,
      mxList: [],
      gysList: [],
      fhbh: '',
      canChange: false,
      pageName: '', // 当前tab页页面
      fileChangeInfo: {},
      filegroup: '',
      fhsj: '',
      yjdhsj: '',
      YjdhsjShow: false,
      date1: '',
      date: '',
      wllxShow: '',
      wllx: '',
      bcshsl: '',
      checked: '',
      fhdbh: '',
      active: '',
      fhdmc: '',
      wlgsmc: '',
      ydh: '',
      fhdd: '',
      wllxr: '',
      wllxfs: '',
      xmmcvalue: '',
      xmbhvalue: '',
      FhsjShow: false,
      showDelete: false,
      SpxzShow: false,
      showSearch: false,
      result: [],
      result2: [],
      fileList: [
        { url: 'https://img.yzcdn.cn/vant/leaf.jpg' }
      ],
      searchPick: {
        PickTitle: '请选择物流类型',
        PickColumns: []
      },
      activeNames: ['1']
    }
  },
  computed: {

  },
  created() {
    if (this.$store.state.fhgl.active) {
      this.active = this.$store.state.fhgl.active
    }
    this.getWllx()
    if (this.$store.state.fhgl.fhbh) {
      this.canChange = true
      this.fhbh = this.$store.state.fhgl.fhbh
    }
    this.getGysFhxx()
  },

  methods: {
    DeleteSp(mxbh, index) {
      this.$dialog.confirm({
        title: '提示',
        message: '确认删除么？'
      }).then(() => {
        this.$store.dispatch('fhgl/deleteXmAction', {
          mxbh: mxbh
        }).then((res) => {
          this.$toast('删除成功！')
          this.fhmxList[this.mxIndex].mxList.splice(index, 1)
          if (this.fhmxList[this.mxIndex].mxList.length === 0) {
            this.fhmxList.splice(this.mxIndex, 1)
            this.SpxzShow = false
          }
          // this.getGysFhxx()
        }).catch(() => {
          this.$toast('删除失败！')
        })
      }).catch(() => {
      })
    },
    goAddSp() {
      this.$router.push({
        name: 'ShglAddSp',
        params: {
          fhmxList: this.fhmxList
        }
      })
    },
    save() {
      this.saveGysFhxx()
    },
    checkMx(items, indexs) {
      this.mxIndex = indexs
      if (items.state) {
        for (let i = 0; i < this.fhmxList[indexs].mxList.length; i++) {
          this.fhmxList[indexs].mxList[i].state = true
        }
      } else {
        for (let i = 0; i < this.fhmxList[indexs].mxList.length; i++) {
          this.fhmxList[indexs].mxList[i].state = false
        }
      }
    },
    close() {
      if (this.checkedMxList.length > 0) {
        this.fhmxList[this.mxIndex].state = true
        this.fhmxList.splice(this.mxIndex, 1, this.fhmxList[this.mxIndex])
      } else {
        this.fhmxList[this.mxIndex].state = false
        this.fhmxList.splice(this.mxIndex, 1, this.fhmxList[this.mxIndex])
      }
    },
    checkFinish() {
      if (this.checkedMxList.length > 0) {
        this.fhmxList[this.mxIndex].state = true
        this.fhmxList.splice(this.mxIndex, 1, this.fhmxList[this.mxIndex])
      } else {
        this.fhmxList[this.mxIndex].state = false
        this.fhmxList.splice(this.mxIndex, 1, this.fhmxList[this.mxIndex])
      }
      this.SpxzShow = false
    },
    checkSp(item, index) {
      if (item.bcsl === '') {
        this.$toast('请选择本次送货数量！')
        this.fhmxList[this.mxIndex].mxList[index].state = !this.fhmxList[this.mxIndex].mxList[index].state
        this.fhmxList[this.mxIndex].mxList.splice(index, 1, this.fhmxList[this.mxIndex].mxList[index])
        return
      }
      if (item.state) {
        this.checkedMxList.push(item)
      } else {
        this.checkedMxList.splice(this.checkedMxList.indexOf(item), 1)
      }
      if (this.checkedMxList.length === this.fhmxList[this.mxIndex].mxList.length) {
        this.checked = true
      } else {
        this.checked = false
      }
    },
    getGysFhxx() {
      this.$store.dispatch('fhgl/getXm_fhdhmxListAction', {
        fhbh: this.fhbh
      }).then((res) => {
        this.xm_fhdh = res.xm_fhdh
        for (let i = 0; i < res.fhmxList.length; i++) {
          const arr = {}
          arr.state = false
          for (let j = 0; j < res.fhmxList[i].length; j++) {
            const item = res.fhmxList[i][j]
            const key = item.name
            const value = item.value
            if (value) {
              arr[key] = value
            }
            if (item.mxList) {
              const array = []
              for (let k = 0; k < item.mxList.length; k++) {
                const element = item.mxList[k]
                const a = {}
                a.state = false
                for (let x = 0; x < element.length; x++) {
                  const temp = element[x]
                  const key = temp.name
                  const value = temp.value
                  if (value) {
                    a[key] = value
                  }
                }
                array.push(a)
              }
              arr['mxList'] = array
            }
          }
          this.fhmxList.push(arr)
        }
        for (let i = 0; i < this.xm_fhdh.length; i++) {
          const item = this.xm_fhdh[i]
          if (item.type === 'calendar' && item.value) {
            item.reqValue = item.value
            item.value = item.value + ''
            item.value = item.value.slice(0, 4) + '-' + item.value.slice(4, 6) + '-' + item.value.slice(6, 8)
          }
          if (item.type === 'file' && item.value) {
            this.filegroup = item.value
          }
          if (item.type === 'select' && item.value) {
            for (let i = 0; i < this.searchPick.PickColumns.length; i++) {
              if (this.searchPick.PickColumns[i].value === item.value) {
                item.reqValue = item.value
                item.value = this.searchPick.PickColumns[i].label
              }
            }
          }
        }
      })
    },
    onClickDisabled(name, title) {
      this.$toast('请先保存发货信息！')
    },
    onClick(data, ref) {
      // for (let i = 0; i < this.fhmxList.length; i++) {
      //   for (let j = 0; j < this.fhmxList[i].mxList.length; j++) {
      //     this.checkedMxList.push(this.fhmxList[i].mxList[j])
      //   }
      // }
      this.pageName = ref
      if (!this.canChange) {
        return false
      }
    },
    // 上传附件回调函数 name:附件名称，fileChanInfo:附件信息
    getFileChangeInfo(name, fileChangeInfo) {
      this.fileChangeInfo['fj'] = fileChangeInfo
    },
    // 保存供应商发货信息
    // 参数{
    //       ryid: this.$store.state.mh.mhUserInfo.id,
    //       zzid: this.$store.state.mh.mhUserInfo.zzid,
    //       id: this.id,
    //       wllx: this.wllx,
    //       fhdmc: this.fhdmc,
    //       wlgsmc: this.wlgsmc,
    //       fhsj: this.fhsj,
    //       ydh: this.ydh,
    //       fhdd: this.fhdd,
    //       wllxr: this.wllxr,
    //       yjdhsj: this.yjdhsj,
    //       wllxfs: this.wllxfs,
    //       fj: this.fileChangeInfo
    //     }
    saveGysFhxx() {
      this.scState = true
      this.$validator.validate().then(result => {
        if (result) {
          const data = {}
          for (let i = 0; i < this.xm_fhdh.length; i++) {
            const item = this.xm_fhdh[i]
            if (item.type === 'calendar') {
              this.$set(data, item.name, item.reqValue)
              continue
            }
            if (item.type === 'file') {
              this.$set(data, item.name, this.fileChangeInfo)
              continue
            }
            if (item.type === 'select') {
              for (let i = 0; i < this.searchPick.PickColumns.length; i++) {
                if (this.searchPick.PickColumns[i].label === item.value) {
                  item.reqValue = this.searchPick.PickColumns[i].value
                }
              }
              this.$set(data, item.name, item.reqValue)
              continue
            }
            this.$set(data, item.name, item.value)
          }
          this.$set(data, 'ryid', this.$store.state.mh.mhUserInfo.id)
          this.$set(data, 'zzid', this.$store.state.mh.mhUserInfo.zzid)
          this.$store.dispatch('fhgl/saveXm_fhdhAction', data).then((res) => {
            this.$toast('保存成功！')
            this.canChange = true
            this.fhbh = res
            this.$store.commit('fhgl/SET_FHBH', res)
            this.id = res
            this.key++
          })
        }
      })
    },
    // 物流类型回调
    confirmPick(pick) {
      this.xm_fhdh[this.xxIndex].value = pick.label
    },
    // 获取物流类型
    getWllx() {
      this.$store
        .dispatch('fhgl/getWllxAction')
        .then(res => {
          const maps = []
          for (var key in res) {
            maps.push({ 'value': key, 'label': res[key] })
          }
          this.searchPick.PickColumns = maps
        })
        .catch((error) => {
          this.$toast.fail(error)
        })
    },
    getContainer() {

    },
    showWllxPopup(index) {
      this.xxIndex = index
      this.$bus.$emit('showPick', true)
    },
    showSpxzPopup(index) {
      this.mxIndex = index
      this.checkedMxList = []
      for (let i = 0; i < this.fhmxList[index].mxList.length; i++) {
        const item = this.fhmxList[index].mxList[i]
        if (item.state) {
          this.checkedMxList.push(this.fhmxList[index].mxList[i])
        }
      }
      if (this.checkedMxList.length === this.fhmxList[index].mxList.length) {
        this.checked = true
      } else {
        this.checked = false
      }
      this.SpxzShow = true
    },
    showFhsjCalendar(index) {
      this.xxIndex = index
      this.FhsjShow = true
    },
    showDeleteBtn() {
      if (!this.fhbh) {
        this.$toast('请先保存发货到货信息！')
        return
      }
      if (this.fhmxList.length === 0) {
        this.$toast('请先选择商品！')
        return
      }
      this.$dialog.confirm({
        title: '提示',
        message: '确认提交当前信息么？'
      }).then(() => {
        this.$store.dispatch('fhgl/submitXm_fhdhAction', {
          fhbh: this.fhbh,
          ryid: this.$store.state.mh.mhUserInfo.id
        }).then((res) => {
          this.$router.push('ShglIndex')
          this.$toast('提交成功！')
        })
      }).catch(() => {
      })
    },
    goShglIndex() {
      this.$store.commit('fhgl/SET_ACTIVE', 0)
      this.$store.commit('fhgl/SET_FHBH', '')
      this.$store.commit('fhgl/SET_SPMXLIST', [])
      this.$router.back()
    },
    formatDate(date) {
      const year = date.getFullYear()
      const month = (date.getMonth() + 1) < 10 ? ('0' + (date.getMonth() + 1)) : date.getMonth()
      const day = date.getDate() < 10 ? ('0' + date.getDate()) : date.getDate()
      return year + '-' + month + '-' + day
    },
    formatDate1(date) {
      const year = date.getFullYear()
      const month = (date.getMonth() + 1) < 10 ? ('0' + (date.getMonth() + 1)) : date.getMonth()
      const day = date.getDate() < 10 ? ('0' + date.getDate()) : date.getDate()
      return year + '' + month + '' + day
    },
    onConfirm(date) {
      this.xm_fhdh[this.xxIndex].value = this.formatDate(date)
      this.xm_fhdh[this.xxIndex].reqValue = this.formatDate1(date)
      this.scState = false
      this.FhsjShow = false
    },
    checkAll() {
      let num1 = 0
      let num2 = 0
      for (let i = 0; i < this.fhmxList[this.mxIndex].mxList.length; i++) {
        if (this.fhmxList[this.mxIndex].mxList[i].state) {
          num1++
        } else {
          num2++
        }
      }
      if (num1 === 0) {
        for (let i = 0; i < this.fhmxList[this.mxIndex].mxList.length; i++) {
          this.fhmxList[this.mxIndex].mxList[i].state = true
          this.fhmxList[this.mxIndex].mxList.splice(i, 1, this.fhmxList[this.mxIndex].mxList[i])
          this.fhmxList.splice(this.mxIndex, 1, this.fhmxList[this.mxIndex])
          this.checkedMxList.push(this.fhmxList[this.mxIndex].mxList[i])
        }
      }
      if (num2 === 0) {
        for (let i = 0; i < this.fhmxList[this.mxIndex].mxList.length; i++) {
          this.fhmxList[this.mxIndex].mxList[i].state = false
          this.fhmxList[this.mxIndex].mxList.splice(i, 1, this.fhmxList[this.mxIndex].mxList[i])
          this.fhmxList.splice(this.mxIndex, 1, this.fhmxList[this.mxIndex])
          this.checkedMxList = []
        }
      }
      if (num1 !== 0 && num2 !== 0) {
        for (let i = 0; i < this.fhmxList[this.mxIndex].mxList.length; i++) {
          if (this.fhmxList[this.mxIndex].mxList[i].state === false) {
            this.checkedMxList.push(this.fhmxList[this.mxIndex].mxList[i])
          }
          this.fhmxList[this.mxIndex].mxList[i].state = true
          this.fhmxList[this.mxIndex].mxList.splice(i, 1, this.fhmxList[this.mxIndex].mxList[i])
          this.fhmxList.splice(this.mxIndex, 1, this.fhmxList[this.mxIndex])
        }
      }
    }
  }

}
</script>

